<template>
    <div class="login">
        <b-container>
            <b-row>
                <div class="log-in">
                    <b-card class="sq" bg-variant="light">
                        <b-form @submit.prevent="handleClick">
                            <b-form-group class="login-text display-6" label="SIGN IN" label-class="font-weight-bold" 
                                label-align="center"></b-form-group>
                            <hr />
                            <b-form-group class="mt-1 label-login" label="Email:" >
                                <b-form-input type="email" v-model="email" placeholder="Enter email" required></b-form-input>
                            </b-form-group>
                            <b-form-group class="mt-3 label-login" label="Password:" >
                                <b-form-input type="password" v-model="pass" aria-describedby="password-help-block" placeholder="Enter password" required></b-form-input>
                            </b-form-group>
                            <b-button block type="submit" class="mt-3 mb-2 btn" variant="primary">Log In</b-button>
                        </b-form>
                    </b-card>
                </div>
                    
            </b-row>
        </b-container>
    </div>
</template>

<script>
import firebase from 'firebase'

export default {
    name: 'Login',
    data() {
        return {
            email: "",
            pass: ""
        }
    },
    methods: {
        handleClick() {
            let email = this.email;
            let password = this.pass;
            firebase.auth().signInWithEmailAndPassword(email, password)
                .then( () => {
                    this.$router.push('/');
                }).catch(function(error) {
                    // Handle Errors here.
                    var errorCode = error.code;
                    var errorMessage = error.message;
                    if (errorCode == 'auth/weak-password') {
                        alert('The password is too weak.'); 
                    } else {
                        alert(errorMessage);
                    }
                    console.log(error);
                    });    

        }
    }
    
}
</script>

<style scoped>

    .login {
        background-image: linear-gradient(180deg, #f6fafd81, #24418683),url('../assets/login.jpg');
        background-position: center;
        background-size: cover;
    }

    .log-in{
        height: 100vh;
        width: 100%;
        display:flex;
        align-items: center;
        justify-content: center;
    }
    .sq{
    width:25rem;
    max-width: 25rem;
    background: #fafafa;
    }

    .btn{
        width:100%;
        font-weight: bold;
        letter-spacing: 2px;
        font-size: 1.25rem;
    }

    .login-text {
        font-weight: bold;
        letter-spacing: 1px;
    }

    .label-login {
        font-size: 1rem;
    }
</style>
