<template>
    <div class="register">
        <b-container>
            <b-row>
                <div class="register-container">
                    <b-card class="sq" bg-variant="light">
                        <b-form @submit.prevent="handleClick" > 
                            <b-form-group 
                                class="sign-up-text display-6"
                                label="SIGN UP" 
                                label-class="font-weight-bold" 
                                label-align="center">
                            </b-form-group>
                            <hr />
                            <b-form-group class="mt-1 label-register" label="First Name:" >
                                <b-form-input type="text" v-model="fname" placeholder="Enter first name" required></b-form-input>
                            </b-form-group>
                            <b-form-group class="mt-2 label-register" label="Last Name:" >
                                <b-form-input type="text" v-model="lname" placeholder="Enter last name" required></b-form-input>
                            </b-form-group>
                            <b-form-group class="mt-2 label-register" label="Email:" >
                                <b-form-input type="text" v-model="email" placeholder="Enter email"></b-form-input>
                            </b-form-group>
                            <b-form-group class="mt-3 label-register" label="Password:" >
                                <b-form-input type="password" v-model="pass" placeholder="Password" ></b-form-input>
                            </b-form-group>
                            <b-form-group class="mt-3 label-register" label="Confirm Password:" >
                                <b-form-input type="password" v-model="cpass"  placeholder="Confirm Password"></b-form-input>
                            </b-form-group>
                            <b-form-group>
                                <b-button block type="submit" class="mt-3 mb-2 btn" variant="primary">Register</b-button>
                            </b-form-group>
                        </b-form>
                    </b-card>
                </div>            
            </b-row>
        </b-container>
        
    </div>
</template>

<script>
import firebase from 'firebase'


export default {
    name: "Signup",
    data() {
        return {
            fname: "",   
            lname: "",
            email: "",
            pass: "",
            cpass: ""
        }   
    },
    methods: {
        handleClick: function() {
            let email = this.email;
            let password = this.pass;

            if(password === this.cpass) {
                firebase.auth().createUserWithEmailAndPassword(email, password)
                .then( () => {
                    this.$router.push('/login');
                }).catch(function(error) {
                    // Handle Errors here.
                    var errorCode = error.code;
                    var errorMessage = error.message;
                    if (errorCode == 'auth/weak-password') {
                        alert('The password is too weak.'); 
                    } else {
                        alert(errorMessage);
                    }
                    console.log(error);
                    });
            } else {
                alert("Your Password is not same. Please try it again.")
            }
                

        }
    }
}
</script>

<style scoped>

    .register {
        background-image: linear-gradient(180deg, #f6fafd81, #24418683),url('../assets/login.jpg');
        background-position: center;
        background-size: cover;
    }

    .register-container {
        height: 100vh;
        width: 100%;
        display:flex;
        align-items: center;
        justify-content: center;
        margin-top:3rem;
    }
    .sq{
        width:25rem;
        max-width: 25rem;
        background: #fafafa;
    }

    .btn{
        width:100%;
        font-weight: bold;
        letter-spacing: 2px;
        font-size: 1.25rem;
    }

    .sign-up-text {
        font-weight: bold;
        letter-spacing: 1px;
    }

    .label-register {
        font-size: 1rem;
    }
</style>