import Vue from 'vue'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import firebase from 'firebase'
import "firebase/app"
import "firebase/database"

const firebaseConfig = {
  apiKey: "AIzaSyD4ZnMtiw_OzIP7-rHyJ_nz6a-MQ0Ac1-Y",
  authDomain: "auth-form-55082.firebaseapp.com",
  projectId: "auth-form-55082",
  storageBucket: "auth-form-55082.appspot.com",
  messagingSenderId: "196100965970",
  appId: "1:196100965970:web:c87190fe0339353fbf418b"
};

firebase.initializeApp(firebaseConfig);



Vue.config.productionTip = false
Vue.use(BootstrapVue)
Vue.use(IconsPlugin)

let app;

firebase.auth().onAuthStateChanged(user => {
  if(!app) {
    app = new Vue({
      el: "#app",
      router,
      store,
      render: function (h) { return h(App) }
    }).$mount('#app')
  }
});