<template>
	<div class="top">
		<b-container>
			<b-navbar toggleable="lg">
				<b-navbar-brand to="/">
					<img src="../assets/logo.png" alt="Logo">
				</b-navbar-brand>
				<b-navbar-toggle target="navbar-toggle-collapse"></b-navbar-toggle>	
				<b-collapse id="navbar-toggle-collapse" is-nav>
					<b-navbar-nav align="center" class="ul">
						<b-nav-item  class="link" to="/">Home</b-nav-item>
						<b-nav-item class="link"  href="#doctors">Doctors</b-nav-item>
						<b-nav-item class="link" href="#reservation">Reservation</b-nav-item>
						<b-nav-item class="link"  href="#reviews">Reviews</b-nav-item>
					</b-navbar-nav>
					<b-nav class="ul" align="right" v-if="user">
						<b-nav-item class="link" >Hi, {{user}}</b-nav-item>
						<b-nav-item class="link"  @click="logout" >Logout <img src="../assets/logout1.png" class="logout" alt="Logo"></b-nav-item>
					</b-nav>
					<b-nav class="ul login-register" align="right" v-else>
						<b-nav-item  class="link"  :to="{name: 'Login'}">Sign In <img src="../assets/log-in.png" class="sign-in" alt="Logo"></b-nav-item>
						<b-nav-item  class="link"  :to="{name: 'Register'}">Sign Up <img src="../assets/add-user.png" class="sign-up" alt="Logo"></b-nav-item>
					</b-nav>
				</b-collapse>
				
				
			</b-navbar>
		</b-container>
	</div>
	
</template>

<script>
import fb from 'firebase'

export default {
	name: "Nav",
	data() {
		return {
			user: ""
		}
	},
	methods: {
		logout() {
			fb.auth().signOut()
				.then(() => {
					this.user = "";
					this.$router.push('/');
				})
				.catch((err) => alert(err.message))

			
		}
		
	},

	mounted() {
		fb.auth().onAuthStateChanged((user) => {
			if(user) {
				this.user = user.email;
			} else {
				this.$router.push('/login');
			}
		})
	},
	created() {	
		if(fb.auth().user) {
			this.user = fb.auth().user.email;
		}


	},
	
}
</script>

<style scoped>

.top{
	width: 100%;
	background-color: #f6fafd;
	position: fixed;
	z-index:50000; 
	box-shadow: 0 1px 10px rgba(0,0,0,0.25), 0 1px 5px rgba(0,0,0,0.22);
}
.ul {
	width: 100%;
}


.nav-item {
	padding: 0 .75rem;
}

a {
	text-decoration: none;
	color: #244286 !important;
	font-weight: bold;
	letter-spacing: 1px;
}

img {
	width: 3rem;
}

.logout, .sign-in, .sign-up {
	width: 1.2rem;
}

.link {
	font-size: 1rem;
}
</style>